@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
html {
  font-size:70%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@media(max-width:1170px)
{
    html{
        font-size: 78%;
    }
}
@media(max-width:1028px)
{
    html{
        font-size: 75%;
    }
}
 
@media(max-width:838px)
{
    html{
        font-size: 67%;
    }
}
@media(max-width:768px)
{
    html{
        font-size: 61%;
    }
}
@media(max-width:644px)
{
    html{
        font-size: 59%;
    }
}
@media(max-width:533px)
{
    html{
        font-size: 45%;
    }
}
@media(max-width:376px)
{
    html{
        font-size: 35%;
    }
}
@media(max-width:285px)
{
    html{
        font-size: 25%;
    }
}

  .section-center{
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    background-color: rgb(206 229 207);
  }
  
  .post-form{
align-items: center;
color: deepskyblue;
font-size: 1em;
margin-top: .3vh;
margin-bottom: .7vh;
margin-right: .5vw ;
width: 75vw;
margin-left: 12.5vw;
}
.post-form h3{
    text-align: center;
    font-size: 3em;
}
  
.post-control{
  align-items: center;
  margin-top: .3vh;
  height: 55vh;
  width: 65vw;
  border-radius: 1.5em;
  margin-left: 5vw;
background-color: #cfafaf;
}
input.post {
  border: .2em solid grey;
  border-radius: .5em;
  margin-top: 2vh;
  padding: 0;
  width: 62vw;
  height: 44vh;
  font-size: 1.5em;
  margin-left: 1.4vw;
  background-color: white;
}

.submit-btn{
  text-align: center;
  height: 5vh;
  width: 17vw;
  font-size: 2em;
  border-radius: .2em;
  margin-left: 22.5vw;
  background-color: #7f7878;
  cursor: pointer;
}
.submit-btn:hover{
  /* background: violet; */
  border: .1em solid #1b201b;
}
.post-container {
background-color: #c6f6bc;
}

.post-list{
display: flex;
flex-wrap: wrap;
border-radius: .3em;
padding-top: 2vh;
padding-bottom: 2vh;
box-sizing: border-box;
}

.post-item{
  border: .1em solid #8d2323;
  height: 26vh;
  width: 23vw;
  margin-top: auto;
  margin-left: 3.5vw;
  margin-right: 3.5vw;
  align-items: center;
  border-radius: .3em;
  font-size: 1.3em;
}

.clear-btn{
  border: .2em solid red;
  height: 6vh;
  border-radius: .3em;
  width: 27vw;
  background-color: #b4c9abb8;
  font-size: 1.8em;
  margin-left: 35vw;
  cursor: pointer;
}
.clear-btn:hover{
  background: lightsalmon;
  border: .2em solid chocolate;
}
.title{
  height: 20vh;
}
.title p{
  width: 23vw;
  font-size: 1.3em;
  padding: 1vh;
}
.btn-container{
  width: 22.8vw;
  display: inline-flex;
  height: 5.5vh;
  border: .1em solid #010000;
  justify-content: space-around;
  align-items: center;
}

.edit-btn{
border-radius: .2em;
background-color: #81e24b;
color: white;
height: 4.2vh;
cursor: pointer;
width: 5vw;
cursor: pointer;
}
.edit-btn:hover{
  border: .23em solid #562bc3;
}
.delete-btn{
  order: .1em solid rgb(9, 62, 236);
  border-radius: .2em;
  background-color: #ec5b5b;
  cursor: pointer;
  height: 4vh;
  width: 7vw;
  cursor: pointer;
}
.delete-btn:hover{
  border: .2em solid #1a1515;
}
.alert {
  margin-bottom: 1em;
  height: 2.5em;
  display: grid;
  align-items: center;
  text-align: center;
  font-size: 1.6em;
  border-radius: 0.25em;
  letter-spacing: .1em;
  margin-left: 0vw;
  text-transform: capitalize;
}
.alert-danger {
  color: #0c20d8;
  background: skyblue;
}
.alert-success {
  color: #13e443;
  background: #ca2b8d;
}

nav{
background-color: rgb(97, 91, 91);
align-items: center;
justify-content: space-around;
display: flex;
height: 12vh;
font-size:2em;
width: 100vw;
}
.nav-links {
    width: 40%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
    font-family: 'Baloo Bhai 2', cursive;
}
.nav-links ul li{
    
    text-decoration: none;
    font-size: 1.3rem;
}

 .form1-container {
    box-shadow: 0 0.6em 1em 0 rgba(0, 0, 0, 0.2), 0 0.8em 2.5em 0 rgba(0, 0, 0, 0.2);
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-around;
    grid-template-columns: 1fr 1fr;
  }

  .form1-content {
    border-radius: 1.25em 0 0 1.25em;
    position: relative;
    background: linear-gradient(90deg, rgb(40, 40, 40) 0%, rgb(17, 17, 17) 100%);
  width: 33vw;
  height: 72vh;
  }
  
  .form1 {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
    
  .form1 h1 {
    font-size: 2em;
    text-align: start;
    width: 80%;
    margin-bottom: 6vh;
    color: #fff;
  }
  
  .form1-inputs {
    margin-bottom: 0.5em;
    width: 80%;
  }
  
  .form1-label {
    display: inline-block;
    font-size: 1.5em;
    margin-bottom: .4em;
    color: #fff;
  }
  
  .form1-input {
    display: block;
    padding-left: 1.25em;
    outline: none;
    border-radius: .25em;
    height: 6vh;
    width: 100%;
    border: none;
    font-size: 1.8em;
  }
  
  .form1-input::-webkit-input-placeholder {
    color: #595959;
    font-size: 1em;
  }
  
  .form1-input:-ms-input-placeholder {
    color: #595959;
    font-size: 1em;
  }
  
  .form1-input::placeholder {
    color: #595959;
    font-size: 1em;
  }

  .form1-inputs p {
    font-size: 1.4em;
    margin-top: 0.3em;
    color: #f00e0e;
  }
  
  
  .form1-input-btn {
    width: 80%;
    height: 2.25em;
    margin-top: 1.25em;
    border-radius: .25em;
    background: linear-gradient(
      90deg,
      rgb(39, 176, 255) 0%,
      rgb(0, 232, 236) 100%
    );
    background: #8a9c9c;
    outline: none;
    border: none;
    color: #fff;
    font-size: 1.9em;
  }
  
  .form1-input-btn:hover {
    cursor: pointer;
    background: linear-gradient(
      90deg,
      rgb(39, 143, 255) 0%,
      rgb(12, 99, 250) 100%
    );
    transition: all 0.4s ease-out;
  }
  
  
  .form1-success {
    text-align: center;
    font-size: 3em;
    margin-top: 10em;
    color: #fff;
  }
  

